@import url('https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200;0,400;0,800;1,400;1,800&display=swap');

:root {
    --box-shadow: 0 0 2rem var(--grey-000);
    --grey-000: #0b0c0e;
    --grey-100: #202225;
    --grey-200: #292b2f;
    --grey-300: #2f3136;
    --grey-400: #36393f;
    --grey-500: #40444b;
    --grey-600: #72767d;
    --grey-700: #a3a1a7;
    --grey-800: #d0d1d3;
    --grey-900: #eeeeee;
    --red-200: #692023;
    --red-400: #863536;
    --red-500: #b8494a;
    --red-600: #e26c6e;
    --red-800: #fcacad;
    --green-200: #1a380d;
    --green-400: #325821;
    --green-500: #4c8534;
    --green-600: #72b953;
    --green-800: #90ff86;
    --bg-texture: url(data:image/png;base64,);
}

* {
    box-sizing: border-box;
    font-family: 'Nunito', sans-serif;
    margin: 0;
    padding: 0;
}

body {
    background: #0b0c0e;
    background: var(--grey-000);
    background-image: url(data:image/png;base64,);
    background-image: var(--bg-texture);
    color: #a3a1a7;
    color: var(--grey-700);
}

button {
    background: linear-gradient(#a3a1a7, #72767d);
    background: linear-gradient(var(--grey-700), var(--grey-600));
    border: 1px solid #202225;
    border: 1px solid var(--grey-100);
    border-bottom: 3px solid #0b0c0e;
    border-bottom: 3px solid var(--grey-000);
    border-radius: 2rem;
    color: #202225;
    color: var(--grey-100);
    display: flex;
    font-size: 1.5rem;
    padding: 0.25rem 0.75rem;
    position: relative;
}

button:disabled {
    background: linear-gradient(#72767d, #40444b);
    background: linear-gradient(var(--grey-600), var(--grey-500));
    border: 1px solid #202225;
    border: 1px solid var(--grey-100);
    border-radius: 2rem;
    box-shadow: inset 1px 1px 2px #292b2f;
    box-shadow: inset 1px 1px 2px var(--grey-200);
    color: #2f3136;
    color: var(--grey-300);
    font-size: 1.5rem;
    padding: 0.25rem 0.75rem;
}

button:enabled {
    box-shadow: 2px 2px 4px #2f3136;
    box-shadow: 2px 2px 4px var(--grey-300);
    cursor: pointer;
    margin-top: -1px;
}

button:enabled:active {
    box-shadow: 1px 1px 2px #2f3136;
    box-shadow: 1px 1px 2px var(--grey-300);
    border-width: 1px;
    margin-top: 1px;
}

button:enabled:hover {
    color: #2f3136;
    color: var(--grey-300);
    background: linear-gradient(#d0d1d3, #a3a1a7);
    background: linear-gradient(var(--grey-800), var(--grey-700));
}

button > .tooltip {
    background: linear-gradient(#2f3136, #202225);
    background: linear-gradient(var(--grey-300), var(--grey-100));
    border: 1px solid #a3a1a7;
    border: 1px solid var(--grey-700);
    border-radius: 0 2rem 2rem 2rem;
    box-shadow: 0 0 2rem #0b0c0e;
    box-shadow: var(--box-shadow);
    color: #d0d1d3;
    color: var(--grey-800);
    font-size: 1.2rem;
    font-weight: 200;
    left: 50%;
    opacity: 0;
    padding: 0.125rem 0.75rem 0.25rem 0.75rem;
    pointer-events: none;
    position: absolute;
    text-align: center;
    top: calc(100% + 1rem);
    white-space: nowrap;
    z-index: 1;
}

button:enabled:active > .tooltip {
    top: calc(100% + 1rem - 2px);
}
  
button:hover .tooltip {
    -webkit-animation: showTooltip 150ms linear 1s 1 normal both;
            animation: showTooltip 150ms linear 1s 1 normal both;
}

@-webkit-keyframes showTooltip {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }    
}

@keyframes showTooltip {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }    
}

button .tooltip::before {
    border-color: transparent transparent #a3a1a7 transparent;
    border-color: transparent transparent var(--grey-700) transparent;
    border-style: solid;
    border-width: 0.6rem;
    bottom: 100%;
    content: " ";
    left: -0.6rem;
    position: absolute;
    top: calc(-0.6rem);
    -webkit-transform: rotate(-135deg);
            transform: rotate(-135deg);
}

button .tooltip::after {
    border-color: transparent transparent #2f3136 transparent;
    border-color: transparent transparent var(--grey-300) transparent;
    border-style: solid;
    border-width: 0.6rem;
    bottom: 100%;
    content: " ";
    left: calc(-0.6rem + 1px);
    position: absolute;
    top: calc(-0.6rem + 3px);
    -webkit-transform: rotate(-135deg);
            transform: rotate(-135deg);
}

.activity {
    display: flex;
    justify-content: center;
    text-align: center;
}

.tray {
    background: linear-gradient(#72767d, #40444b);
    background: linear-gradient(var(--grey-600), var(--grey-500));
    border: 1px solid #a3a1a7;
    border: 1px solid var(--grey-700);
    border-radius: 1.5rem;
    box-shadow: 0 0 2rem #0b0c0e;
    box-shadow: var(--box-shadow);
    color: #202225;
    color: var(--grey-100);
    display: flex;
    font-size: 1.5rem;
    gap: 0.5rem;
    margin: auto;
    padding: 0.5rem;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}